<template>
  <div class="row">
    <modal :show="modalStatus">
      <h6 class="modal-title" id="modal-title-default">Add Organizer</h6>

      <p class="mt-4">Please Fill these data to add new <strong>Organizer</strong></p>

      <form @submit.prevent="">
        <div class="d-flex flex-column">
          <input-component
            @input="name"
            type="text"
            placeholder="name"
            name="name"
            size="md"
            :value="form.name"
            :valid="formValidation.name.isValid"
            :startValidate="formValidation.name.startValidate"
            :errorMessage="formValidation.name.errorMessage"
          />
          <input-component
            @input="slogan"
            type="text"
            placeholder="slogan"
            name="slogan"
            size="md"
            :value="form.slogan"
          />
          <input-component
            @input="description"
            type="text"
            placeholder="description"
            name="description"
            size="md"
            :value="form.password"
          />

          <CheckBoxComponent @input="isDisabled" :id="'add-admin-isDisabled'">Is Disabled</CheckBoxComponent>
        </div>

        <div class="d-flex justify-content-between gap-1">
          <button-component
            class="mt-4"
            variant="gradient"
            color="success"
            size="sm"
            :isLoading="actionInProgress.loading"
            :disabled="actionInProgress.loading"
            @click="addOrgnazier()"
          >
            Submit
          </button-component>
          <button-component class="mt-4" variant="gradient" color="danger" size="sm" @click="toggleModal()">
            Close
          </button-component>
        </div>
      </form>
    </modal>
  </div>
</template>

<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';
  import CheckBoxComponent from '@/components/Checkbox';

  export default {
    name: 'add-organizer',
    components: { Modal, ButtonComponent, InputComponent, CheckBoxComponent },
    data() {
      return {
        form: {
          name: '',
          slogan: '',
          description: '',
          isDisabled: false,
          imageUrl: '',
        },
        formValidation: {
          name: { isValid: false, startValidate: false, errorMessage: '' },
        },
      };
    },
    computed: {
      modalStatus() {
        return this.$store.state.OrganizersStore.modals.addOrganizer;
      },
      actionInProgress() {
        return this.$store.state.OrganizersStore.progress.addOrganizers;
      },
      formStatus() {
        return this.formValidation.name.isValid;
      },
    },

    methods: {
      name(event) {
        this.form.name = event.target.value;
      },
      slogan(event) {
        this.form.slogan = event.target.value;
      },
      description(event) {
        this.form.description = event.target.value;
      },
      isDisabled(event) {
        this.form.isDisabled = event.target.checked;
      },
      toggleModal() {
        this.$store.commit('OrganizersStore/TOGGLE_ADD_ORGANIZER_MODAL');
      },
      checkForm() {
        this.formValidation.name.startValidate = true;
        if (!this.form.name.length) {
          this.formValidation.name.isValid = false;
          this.formValidation.name.errorMessage = 'Name Field is required';
        } else {
          this.formValidation.name.isValid = true;
        }
      },
      addOrgnazier() {
        this.checkForm();
        if (this.formStatus) {
          this.$store.dispatch('OrganizersStore/addOrganizer', this.form);
        }
      },
    },
  };
</script>
